.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.wrapper {
  display: flex;
  width: 100%;
}

.navbar {
  width: 100%;
  background-color: #6e5dbb;
}

.sidebar-nav {
  width: 120px;
  position: absolute;
  top: 50px;
  left: 0;
  height: 100%;
  background: rgb(188, 192, 182);
  color: black;
  transition: all 0.3s;
}

.list-unstyled .dropdown-toggle {
  font-family: "Courier New", Courier, monospace;
  font-size: 13px;
  color: black;
}

.list-unstyled .locations {
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  font-size: 11px;
  color: blue;
  text-align: left;
}

.list-unstyled .allrentals {
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  font-size: 12px;
  color: black;
  text-align: left;
}

.rentalSection {
  position: relative;
  left: 130px;
  top: -20px;
  width: 1200px;
}

.registerform {
  margin-left: 100px;
  width: 45%;
  border: 2px solid rgb(114, 145, 127);
  padding: 10px;
  font: 12px arial, sans-serif;
}

.registerform input[type="text"] {
  width: 500px;
}

.registerform input[type="password"] {
  width: 500px;
}

table.regtable {
  width: 100%;
}

table.regtable td {
  height: 30px;
  padding: 3px;
  text-align: left;
}

table.logintable {
  width: 100%;
}

table.logintable td {
  height: 30px;
  padding: 3px;
  text-align: left;
}

table.navtable {
  width: 20%;
}

table.navtable td {
  text-align: left;
  font-size: 12px;
}

.navtable .navtablenavlink {
  color: white;
}

.registerform button {
  background-color: #4caf50; /* Green */
  border: none;
  color: white;
  padding: 3px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 12px;
}

.loginform {
  margin-left: 100px;
  width: 45%;
  border: 2px solid rgb(114, 145, 127);
  padding: 10px;
  font: 12px arial, sans-serif;
}

.loginform input[type="text"] {
  width: 500px;
}

.loginform input[type="password"] {
  width: 500px;
}

.loginform button {
  background-color: #4caf50; /* Green */
  border: none;
  color: white;
  padding: 3px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 12px;
}

ul {
  list-style: none; /* Remove list bullets */
  padding: 0;
  margin: 0;
}

li a {
  display: block;
  padding-left: 3px;
}

#navtop li.nav-top-list a {
  color: azure;
  font-family: arial;
  font-size: 12px;
  text-align: center;
  text-transform: uppercase;
  padding: 1px 1px 1px 1px;
}

.maintoast {
  background-color: aqua;
}

.searchlabel {
  font-size: 12px;
  padding: 3px;
}

.searchbutton {
  background-color: #4caf50; /* Green */
  border: none;
  color: white;
  padding: 3px 6px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 10px;
  border-radius: 4px;
}
